import React, { useState, useEffect } from "react";
import InputBase from "@material-ui/core/InputBase";
import { Box, Card, Link } from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import { makeStyles, fade } from "@material-ui/core/styles";
import categoryList from "../../JSONFiles/categoryList";

const useStyles = makeStyles((theme) => ({
  search: {
    position: "relative",
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    "&:hover": {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      marginLeft: theme.spacing(3),
      width: "auto",
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  inputRoot: {
    color: "inherit",
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create("width"),
    width: "100%",
    [theme.breakpoints.up("md")]: {
      width: "20ch",
    },
  },
}));

function Search() {
  const classes = useStyles();
  const [productLists, setProductLists] = useState([]);
  const [search, setSearch] = useState("");
  const [filteredProductList, setFilteredProductList] = useState([]);

  useEffect(() => {
    setProductLists(categoryList);
  }, []);

  // const pr = [];

  // for (let i = 0; i < categoryList.length; i++) {
  //   for (let j = 0; j < categoryList[i].productList.length; j++) {
  //     pr.push(categoryList[i].productList[j]);
  //   }
  // }

  // console.log(pr);

  useEffect(() => {
    setFilteredProductList(
      productLists.filter((product) => product.productList[0].productName.toLowerCase().includes(search.toLowerCase()))
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [search]);

  return (
    <div>
      <div className={classes.search}>
        <div className={classes.searchIcon}>
          <SearchIcon />
        </div>
        <InputBase
          placeholder="Search for a product"
          classes={{
            root: classes.inputRoot,
            input: classes.inputInput,
          }}
          inputProps={{ "aria-label": "search" }}
          onChange={(e) => {
            setSearch(e.target.value);
            var searchFieldTextCount = e.target.value.length;
            if (searchFieldTextCount === 0) {
              return setProductLists([]);
            }
            setProductLists(categoryList);
          }}
        />
      </div>
      <Box display="flex" justifyContent="center">
        <Card className="searchResultCard">
          {filteredProductList.map((productli, idx) => (
            <Link
              color="inherit"
              underline="none"
              onClick={() => {
                setFilteredProductList([]);
              }}
              href="#product"
            >
              <ProductDetail key={idx} {...productli} />
            </Link>
          ))}
        </Card>
      </Box>
    </div>
  );
}

const ProductDetail = (props) => {
  const { categoryName } = props;

  return <p className="roboto p-3 searchResultEach">{categoryName}</p>;
};

export default Search;
