import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import logo from "../../image/logo.svg";
import { Container, IconButton, SwipeableDrawer, List, ListItem, ListItemText, Button, Box } from "@material-ui/core";
import MenuIcon from "@material-ui/icons/Menu";
// import { NavLink } from "react-router-dom";
// import Button from "@material-ui/core/Button";
import SearchIcon from "@material-ui/icons/Search";
import Search from "./Search";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  title: {
    flexGrow: 1,
  },
  list: {
    width: 250,
  },
}));

export default function ButtonAppBar() {
  const classes = useStyles();
  const [hideSearchIcon, setHideSearchIcon] = useState("block");
  const [showSearchField, setShowSearchField] = useState("none");

  const [state, setState] = useState(false);

  const navbarKey = [
    {
      name: "HOME",
      slug: "/movinLabs",
    },
    {
      name: "ABOUT",
      slug: "#about",
    },
    {
      name: "SHOP",
      slug: "#product",
    },
    {
      name: "PAGES",
      slug: "#pages",
    },
    {
      name: "BLOG",
      slug: "#blog",
    },
    {
      name: "CONTACT",
      slug: "#contact",
    },
  ];

  function handleClick() {
    setState(true);
  }

  return (
    <div className={classes.root} id="home">
      <AppBar position="static" className="navbarStyle">
        <Container>
          <Toolbar>
            <span className={classes.title}>
              <img src={logo} alt="Movin Lab Logo" className="logoStyle" />
            </span>
            {navbarKey.map((menu, index) => (
              // <NavLink exact to={menu.slug} href="#product" className="nav rubik p-15 fs-14 mob-Dis-None">
              //   {menu.name}
              // </NavLink>
              <a href={menu.slug} className="nav rubik p-15 fs-14 mob-Dis-None">
                {menu.name}
              </a>
            ))}
            <Box display={hideSearchIcon}>
              <Button
                onClick={() => {
                  setHideSearchIcon("none");
                  setShowSearchField("block");
                }}
              >
                <SearchIcon className="rubik p-15" />
              </Button>
            </Box>
            <Box display={showSearchField}>
              <Search />
            </Box>
            <IconButton edge="start" onClick={handleClick} className="mob-Dis-Block" color="inherit" aria-label="menu">
              <MenuIcon />
            </IconButton>
          </Toolbar>
          <SwipeableDrawer anchor="right" open={state} onClose={() => setState(false)} onOpen={() => setState(true)}>
            <div
              className={classes.list}
              onClick={() => {
                setState(false);
              }}
            >
              <List className="rubik">
                {navbarKey.map((menu, index) => (
                  // <NavLink exact to={menu.slug} activeClassName="activeNavLink" className="navMob rubik fs-14">
                  //   <ListItem button key={index}>
                  //     <ListItemText>{menu.name}</ListItemText>
                  //   </ListItem>
                  // </NavLink>
                  <a href="#product" className="navMob rubik fs-14">
                    <ListItem button key={index}>
                      <ListItemText>{menu.name}</ListItemText>
                    </ListItem>
                  </a>
                ))}
              </List>
            </div>
          </SwipeableDrawer>
        </Container>
      </AppBar>
    </div>
  );
}
