/* eslint-disable eqeqeq */
import {
  Container,
  Grid,
  Typography,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Select,
  FormControl,
  Box,
} from "@material-ui/core";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import categoryList from "../../JSONFiles/categoryList";

const useStyles = makeStyles((theme) => ({
  item2: {
    order: 3,
    marginBottom: "30px",
    [theme.breakpoints.up("md")]: {
      order: 2,
    },
  },
  item3: {
    order: 2,
    marginBottom: "30px",
    [theme.breakpoints.up("md")]: {
      order: 3,
    },
  },
}));

function ProductList() {
  const classes = useStyles();
  const [selectedProduct, setSelectedProduct] = useState(101);
  const [selectedCategoryId, setSelectedCategoryId] = useState(1);

  // for(let i=1; i<categoryList.length; i++) {
  //   categoryList.map((category) => (
  //   selectedCategoryId == i &&  setSelectedProduct(category.productList[0].productListId)
  //   ))
  // }

  function handleChange(event) {
    setSelectedCategoryId(event.target.value);
    for (let i = 1; i < categoryList.length; i++) {
      categoryList.map(
        (category) =>
          // eslint-disable-next-line eqeqeq
          category.categoryListId == event.target.value && setSelectedProduct(category.productList[0].productListId)
      );
    }
  }
  return (
    <div id="product">
      <Container maxWidth="md">
        <Grid container spacing={2} className="aboutUsStyle1">
          <Grid md={4} xs={12}>
            <Box display="flex" alignItems="center" style={{ height: "100%", width: "100%" }}>
              <Typography variant="h4" className="fontPoppins aboutStyle1">
                PRODUCTS
              </Typography>
            </Box>
          </Grid>
          <Grid md={8} xs={12}>
            <Typography variant="h4" className="fontPoppins aboutStyle2">
              <FormControl variant="outlined" className={classes.formControl} style={{ width: "100%" }}>
                <Select native onChange={handleChange} className="categoryNameStyle listPink">
                  {categoryList.map((category) => (
                    <option value={category.categoryListId}>{category.categoryName}</option>
                  ))}
                </Select>
              </FormControl>
            </Typography>
          </Grid>
        </Grid>
      </Container>
      {categoryList.map((category) => (
        <div className="blackBack">
          {selectedCategoryId == category.categoryListId && (
            <Container maxWidth="md">
              <div className="pic-container mobMb-60-30" style={{ left: "3px", top: "102px" }}>
                <div className="pic-row">
                  <div id="divd" style={{ width: "max-content" }}>
                    <Grid container>
                      {category.productList.map((product) => (
                        <Grid item key={product.productListId}>
                          <Typography
                            variant="body2"
                            className={
                              product.productListId === selectedProduct ? "productListTabsActive" : "productListTabs"
                            }
                            onClick={() => {
                              setSelectedProduct(product.productListId);
                            }}
                          >
                            {product.productName}
                          </Typography>
                        </Grid>
                      ))}
                    </Grid>
                  </div>
                </div>
              </div>
            </Container>
          )}
        </div>
      ))}
      {categoryList.map((category) => (
        <Container maxWidth="md">
          {category.productList.map(
            (product) =>
              selectedProduct == product.productListId && (
                <div>
                  <div className="fontPoppins aboutStyle mobMb-30-15">
                    <Typography variant="h6" className="mobCenter">
                      {product.productFullName ? product.productFullName : product.productName}
                    </Typography>
                  </div>
                  <Grid container className="productListMB30">
                    <Grid md={4} xs={12} className={classes.item2}>
                      <Typography variant="h6">PRODUCT DETAILS</Typography>

                      <List>
                        {product.productDetails.map((products) => (
                          <ListItem className="p-0">
                            <ListItemIcon>
                              <CheckCircleIcon />
                            </ListItemIcon>
                            <ListItemText primary={products} className="list15pixel" />
                          </ListItem>
                        ))}
                      </List>
                    </Grid>
                    <Grid md={8} xs={12} className={classes.item3} style={{ margin: "auto 0", marginBottom: "30px" }}>
                      <img src={product.productImage} alt="" style={{ width: "100%" }} />
                    </Grid>
                  </Grid>
                </div>
              )
          )}
        </Container>
      ))}
    </div>
  );
}

export default ProductList;
